import React, { Component } from 'react';
import '../components/LabelText.css';

export class LabelText extends Component{
    render(){

        const { name, value, unit} = this.props;
        return(
            <div className='extras'>
                    <div className='extra-data'>{value}<span dangerouslySetInnerHTML={{ __html: unit }}></span></div>
                    <div className='extra-label'>{name}</div>
                </div>
        )
    }
}

export default LabelText;